document.addEventListener('DOMContentLoaded', function (){
    iniciarApp();
});

function iniciarApp(){
    navegacionFija();
    menuHam();
};

function navegacionFija(){
    const barra = document.querySelector('.header');
    const body = document.querySelector('body');

    window.addEventListener('scroll', function(){
        if(body.getBoundingClientRect().top<0){
            barra.classList.add('fijar-barra');
            body.classList.add('body-scroll');
        } else{
            barra.classList.remove('fijar-barra');
            body.classList.remove('body-scroll');
        }
    });
};

function menuHam (){
    const ham = document.querySelector('.ham');
    const menu = document.querySelector('.menu');
    const cierres = document.querySelectorAll('.cierre')
    ham.addEventListener('click', function() {
        menu.classList.toggle('visible');
        console.log('hamburger button clicked');
    });
    cierres.forEach(cierre => {
        cierre.addEventListener('click', function(){
        menu.classList.remove('visible');
    });
});
}

//Funcionalidad del correo
const form = {
    nombre:'',
    email:'',
    mensaje:''
}
const nombre = document.querySelector('#nombre');
const email = document.querySelector('#email');
const mensaje = document.querySelector('#mensaje');
const formulario = document.querySelector('.formulario');
const btn = document.getElementById('button');

nombre.addEventListener('input', leerEntrada);
email.addEventListener('input', leerEntrada);
mensaje.addEventListener('input', leerEntrada);

function leerEntrada(e){
    form[e.target.id] = e.target.value;
};
formulario.addEventListener('submit', function(e){
    e.preventDefault();
    const {nombre, email, mensaje} = form;
    if(nombre === '' || email ==='' || mensaje ===''){
        alert('Todos los campos son necesarios');
        return;
    }
    btn.value = 'Enviando...';
    const serviceID = 'default_service';
    const templateID = 'template_ruzbuik';
    emailjs.sendForm(serviceID, templateID, this)
    .then(() => {
        btn.value = 'Enviar';
        alert('Correo Enviado Con Exito!!!');
    }, (err) => {
        btn.value = 'Enviar';
        alert(JSON.stringify(err));
    });
});